"use strict";
var util = {};
util.storage = {};
util.pagina = {};
util.alert = {};

// Utilidades para LocalStorage
util.storage.get = function (item) {
    if (localStorage.getItem(item) != null) {
        return JSON.parse(localStorage.getItem(item));
    } else {
        return null;
    }
};

util.storage.set = function (item, data) {
    localStorage.setItem(item, JSON.stringify(data));
};

util.storage.delete = function (item) {
    localStorage.removeItem(item);
};

util.limpiarStorage = function () {
    console.log("Limpiaremos el localStorage");
    localStorage.clear();
};

// Utilidades para alertas
util.alert.success = function (entorno, titulo, mensaje) {
    entorno.$swal({
        toast: true,
        position: "top",
        showConfirmButton: false,
        timer: 1500,
        timerProgressBar: true,
        icon: 'success',
        title: titulo,
        text: mensaje
    });
};

// Otras Utilidades
util.cerrarSesion = function (entorno) {
    console.log("Cerraremos la sesion");
    localStorage.clear();
    entorno.$router.push("/login");
};

util.pagina.go = function (entorno, pagina) {
    entorno.$router.push(pagina);
};

util.pagina.anterior = function (entorno) {
    entorno.$router.go(-1);
};

/* harmony default export */ __webpack_exports__["a"] = (util);

